import React from 'react';
import welcomeBtm from "../assets/welcomeBtm.svg"

export default function WelcomeScreen(props){
  return(
    <div>
    <div className="v-welcome-screen page-center">
       
       <img className="v-welcome-img" src={props.icon} />
       <div className="v-welcome-text-wrapper"><h1 className="v-welcome-header">{props.title}</h1>
      <p>{props.text}
     </p>
     <button className="v-welcome-btn clickable">Get a Quote</button>
       </div>
       
     </div>
     <img src={welcomeBtm} style={{width:"100%",position:"relative",top:"-6px"}}/> 
    </div>
  
  )
  }